#root {
  margin-left: 0;
  text-align: center;
}
body {
  background-color: #e7e7e7;
  font-family: Arial, Helvetica, sans-serif;
}
html {
  margin-bottom: 100px;
  margin-top: 110px;
  direction: ltr;
}
#cardsContainer {
  margin-left: 0px;
  transition: 500ms;
}

/*--------------------------------- Pc English ---------------------------------*/

.cardPc {
  position: relative;
  background-repeat: no-repeat;
  background-size: contain;
  background-color: white;
  display: inline-block;
  width: 250px;
  height: 400px;
  margin: 20px;
  border: solid 1px #effffa;
  border-radius: 50px 0px 50px 0px;
  -moz-border-radius: 50px 0px 50px 0px;
  -webkit-border-radius: 50px 0px 50px 0px;
  transition: all 500ms;
}
.cardPc:hover {
  -webkit-box-shadow: 0px 0px 35px 3px rgba(0,0,0,0.53);
  -moz-box-shadow: 0px 0px 35px 3px rgba(0,0,0,0.53);
  box-shadow: 0px 0px 35px 3px rgba(0,0,0,0.53);
  background-blend-mode: multiply; 
  border: solid 1px transparent;
  border-radius: 0px 50px 0px 50px;
  -moz-border-radius: 0px 50px 0px 50px;
  -webkit-border-radius: 0px 50px 0px 50px;
  background-color: gray;
}
.cardPc:active {
  border-radius: 50px 50px 50px 50px;
  -moz-border-radius: 50px 50px 50px 50px;
  -webkit-border-radius: 50px 50px 50px 50px;
  background-color: rgb(71, 71, 71);
}
.kcalDetailsPcEn {
  position: inherit;
  top: 0px;
  left: 0px;
  margin: 5px;
  border: none;
  color: white;
  font-weight: lighter;
  text-align: left;
  font-size: 18px;
  visibility: hidden;
  transition: visibility 200ms, top 500ms, color 500ms, font-size 500ms;
}
.cardPc:hover > .kcalDetailsPcEn {
  visibility: visible;
  top: 200px; 
  color: white; 
}
.titlePcEn {
  position: absolute;
  bottom: 15px;
  left: 10px;
  color: #393c45;
  font-size: 25px;
  transition: all 500ms;
}
.cardPc:hover > .titlePcEn {
  color: rgb(162, 244, 255);
  text-align: center;
  bottom: 250px;

}
.cardPc p {
  text-align: right;
  display: inline;
  position: absolute;
  right: 5px;
  margin: 0px;
  color: rgb(162, 244, 255);
}
h4, h5, h6 {margin: 0mm; text-align: left;transition: all 500ms;}
h5 {color: #48d2c1; margin-top: 5px;}
h6 {margin-top: 5px; color: #51575b;}

.cardPc:hover h6, .cardPc:hover h5, .cardPc:hover h4 {
  color: rgb(162, 244, 255);
  text-align: center;
  bottom: 250px;
  display: inline-block;
}
#searchInput {
  display: inline-block;
  margin: auto;
  margin-bottom: 10px;
  margin-top: 10px;
  font-size: 20px;
  border-radius: 20px 0px 20px 0px;
  -moz-border-radius: 20px 0px 20px 0px;
  -webkit-border-radius: 20px 0px 20px 0px;
  border: solid 1px #cecece;
  padding: 10px;
  width: 220px;
  -webkit-box-shadow: inset 0px 0px 44px -24px rgba(0,0,0,0.47);
  -moz-box-shadow: inset 0px 0px 44px -24px rgba(0,0,0,0.47);
  box-shadow: inset 0px 0px 44px -24px rgba(0,0,0,0.47);
  transition: all 500ms;
}
#searchInput:focus {
  width: 240px;
  border-radius: 0px 20px 0px 20px;
  -moz-border-radius: 0px 20px 0px 20px;
  -webkit-border-radius: 0px 20px 0px 20px;
  -webkit-box-shadow: 0px 0px 27px -7px rgba(0,0,0,1);
  -moz-box-shadow: 0px 0px 27px -7px rgba(0,0,0,1);
  box-shadow: 0px 0px 27px -7px rgba(0,0,0,1);
  background-color: white;
}
#searchInput:hover {
  width: 240px;
  border-radius: 0px 20px 0px 20px;
  -moz-border-radius: 0px 20px 0px 20px;
  -webkit-border-radius: 0px 20px 0px 20px;
  -webkit-box-shadow: 0px 0px 27px -7px rgba(0,0,0,1);
  -moz-box-shadow: 0px 0px 27px -7px rgba(0,0,0,1);
  box-shadow: 0px 0px 27px -7px rgba(0,0,0,1);
}
.srchConrainer {
  position: fixed;
  right: 0px;
  top: 0px;
  z-index: 1;
  background-color: #e7e7e7;
  width: 100%;
  text-align: center;
  -webkit-box-shadow: 0px 0px 10px 5px rgba(0,0,0,0.10);
  -moz-box-shadow: 0px 0px 10px 5px rgba(0,0,0,0.10);
  box-shadow: 0px 0px 10px 5px rgba(0,0,0,0.10);
  transition: top 300ms;
}
#gram {
  font-weight: bold;
  text-align: center;
  width: 45px;
  border-radius: 5px;
  border: solid 1px #cecece;
  -webkit-box-shadow: inset 0px 0px 44px -24px rgba(0,0,0,0.47);
  -moz-box-shadow: inset 0px 0px 44px -24px rgba(0,0,0,0.47);
  box-shadow: inset 0px 0px 44px -24px rgba(0,0,0,0.47);
  visibility: hidden;
  display: none;
}
.cardPc:hover #gram {
  visibility: visible;
  display: inline-block;
}
.cardPc:hover span {
  display: none;
}
#lang {
  background-color: transparent;
  border: none;
  margin-left: 20px;
  margin-right: 20px;
  font-weight: bold;
  transition: all 500ms;
}
#lang:hover {
  color: #48d2c1;
  font-size: 18px;
  cursor: pointer;
}
#sort {
  background-color: transparent;
  border: none;
  margin-bottom: 5px;
  margin-right: 5px;
  font-weight: bold;
  font-size: 15px;
  transition: all 500ms;
}
#sort:hover {
  background-color: #e7e7e7;
  color: #48d2c1;
  cursor: pointer;
}

/*--------------------------------- Phone English ---------------------------------*/

.cardPhone {
  position: relative;
  background-repeat: no-repeat;
  background-size: contain;
  background-color: white;
  display: inline-block;
  width: 90%;
  height: 100px;
  margin-bottom: 25px;
  border: solid 1px #effffa;
  border-radius: 50px 0px 50px 0px;
  -moz-border-radius: 50px 0px 50px 0px;
  -webkit-border-radius: 50px 0px 50px 0px;
  transition: all 600ms
}
.cardPhone:hover {
  -webkit-box-shadow: 0px 0px 35px 3px rgba(0,0,0,0.53);
  -moz-box-shadow: 0px 0px 35px 3px rgba(0,0,0,0.53);
  box-shadow: 0px 0px 35px 3px rgba(0,0,0,0.53);
  background-blend-mode: multiply; 
  border: solid 1px transparent;
  border-radius: 30px 30px 30px 30px;
  -moz-border-radius: 30px 30px 30px 30px;
  -webkit-border-radius: 30px 30px 30px 30px;
  background-color: gray;
  height: 350px;
}
.cardPhone p {
  text-align: right;
  display: inline;
  position: absolute;
  right: 5px;
  margin: 0px;
  color: rgb(162, 244, 255);
}
.titlePhoneEn {
  position: absolute;
  bottom: 20px;
  width: 75%;
  left: 32%;
  text-align: center;
  color: #393c45;
  transition: all 200ms;
}
.titlePhoneEn h6, .titlePhoneEn h5, .titlePhoneEn h2 {
  display: inline-block;
  margin: 0;
}
.cardPhone:hover > .titlePhoneEn {
  color: rgb(162, 244, 255);
  text-align: center;
  font-size: 20px;
  left: 12%;
  top: 15px;
}
.cardPhone:hover h6, .cardPhone:hover h5, .cardPhone:hover h4 {
  color: rgb(162, 244, 255);
  text-align: center;
  bottom: 250px;
}
.kcalDetailsPhoneEn {
  position: inherit;
  width: 250px;
  margin: 5px;
  border: none;
  color: white;
  font-weight: lighter;
  text-align: center;
  font-size: 2px;
  visibility: hidden;
  transition: visibility 200ms, top 500ms, color 500ms, font-size 500ms;
}
.cardPhone:hover .kcalDetailsPhoneEn {
  visibility: visible;
  top: 140px; 
  color: white; 
  text-align: left;
  font-size: 19px;
  display: inline-block;
}
.cardPhone:hover #gram {
  visibility: visible;
  display: inline-block;
}
.cardPhone:hover span {
  display: none;
}

