/*--------------------------------- Pc Arabic ---------------------------------*/

.kcalDetailsPcAr {
  position: inherit;
  top: 0px;
  margin: 10px;
  border: none;
  color: white;
  font-weight: lighter;
  text-align: right;
  font-size: 18px;
  font-family: 'Amiri', serif;
  visibility: hidden;
  transition: visibility 200ms, top 500ms, color 500ms, font-size 500ms;
}
.kcalDetailsPcAr p {
  right: 155px;
  direction: rtl;
  text-align: right;
}
.cardPc:hover > .kcalDetailsPcAr {
  visibility: visible;
  top: 200px; 
  color: white; 
}
.titlePcAr {
  position: absolute;
  bottom: 15px;
  right: 30px;
  width: 250px;
  color: #393c45;
  font-size: 30px;
  font-family: 'Amiri', serif;
  direction: rtl;
  transition: all 500ms;
}
.titlePcAr h6, .titlePcAr h5, .titlePcAr h4 {
  margin: 0;
  text-align: right;
}
.cardPc:hover > .titlePcAr h6, .cardPc:hover > .titlePcAr h5, .cardPc:hover > .titlePcAr h4 {
  right: 0;
}
.cardPc:hover > .titlePcAr {
  color: rgb(162, 244, 255);
  text-align: center;
  bottom: 250px;
  right: 0;
  display: grid;
}

/*--------------------------------- Pc Kcal Sum ---------------------------------*/

#diaryContainerPc {
  position: fixed;
  top : 150px;
  left: 5px;
  width: 180px;
  margin: 5px;
  z-index: 1;
  overflow-y: scroll;
  overflow: auto;
  max-height: 70%;
  transition: 500ms;
}
#itemsContainerPc {
  position: relative;
  margin-top: 4px;
  border-radius: 5px;
  background-color: white;
  width: 160px;
  transition: 400ms;
}
#itemsContainerPc:hover p, #itemsContainerPc:hover #itemDetailsPc {
  font-size: 18px;
}
#itemsContainerPc:hover #deleteButtonPc {
  transform: rotate(90deg);
}

#itemsContainerPc p {
  text-align: center;
  color: #393c45;
  padding: 2px;
  width: initial;
  transition: 400ms;
}
.itemNamePc {
  margin-top: 0px;
  margin-left: 5px;
  margin-bottom: 0px;
  font-weight: bold;
  transition: 500ms;
}
#itemDetailsPc {
  font-size: 12px;
  margin: 0px;
  margin-left: 5px;
  transition: 500ms;
}
#sumPc {
  background-color: ivory;
  border-radius: 5px;
  width: 160px;
  height: 30px;
  text-align: center;
  display: none;
  transition: 500ms;
}
#sumPc p {
  margin: 0px;
  margin-top: 5px;
  text-align: center;
  display: inline-block;
  transition: 500ms;
}
#deleteButtonPc {
  background-color: transparent;
  position: absolute;
  right: 2px;
  top: 2px;
  padding: 0px;
  font-size: 15px;
  border: transparent;
  transition: 200ms;
}
#deleteButtonPc:hover {
  color: #48d2c1;
  cursor: pointer;
}
#addButtonPc {
  background-color: transparent;
  position: absolute;
  right: 2px;
  top: 2px;
  font-size: 20px;
  border: transparent;
  transition: 500ms;
}
#addButtonPc:hover {
  color: #48d2c1;
  cursor: pointer;
}
.cardPc:hover #addButtonPc {
  right: 12px;
  top: 12px;
  color: white;
  font-size: 30px;
  transform: rotate(90deg);
}

